import { Component, OnInit } from '@angular/core';
import { Plugins } from '@capacitor/core';

const { Browser } = Plugins;
@Component({
  selector: 'app-coinpot',
  templateUrl: './coinpot.page.html',
  styleUrls: ['./coinpot.page.scss'],
})
export class CoinpotPage implements OnInit {

  constructor() {
    Browser.addListener('browserPageLoaded', () => {
      console.log('browserPageLoaded event called');
    });
    Browser.addListener('browserFinished', () => {
      console.log('browserFinished event called');
    });
    Browser.prefetch({
      urls: [
        'https://coinpot.co/dashboard',
        'http://moonbit.co.in',
        'http://moonbitcoin.cash/faucet',
        'http://moonliteco.in',
        'http://moondash.co.in/faucet',
        'http://moondoge.co.in/',
        'http://bonusbitcoin.co/faucet',
        'http://bitfun.co/games'
      ]
    });
  }

  ngOnInit() {
  }
  async openCoinpot() {
    await Browser.open({ toolbarColor: '#f4dc41', url: 'https://coinpot.co/dashboard' });
  }
  async openMoonBitcoin() {
    await Browser.open({ toolbarColor: '#f4dc41', url: 'http://moonbit.co.in' });
  }
  async openMoonBitcoinCash() {
    await Browser.open({ toolbarColor: '#f4dc41', url: 'http://moonbitcoin.cash/faucet' });
  }
  async openMoonLitecoin() {
    await Browser.open({ toolbarColor: '#f4dc41', url: 'http:///moonliteco.in' });
  }
  async openMoonDash() {
    await Browser.open({ toolbarColor: '#f4dc41', url: 'http://moondash.co.in/faucet' });
  }
  async openMoonDoge() {
    await Browser.open({ toolbarColor: '#f4dc41', url: 'http://moondoge.co.in/' });
  }
  async openBonusBitcoin() {
    await Browser.open({ toolbarColor: '#f4dc41', url: 'http://bonusbitcoin.co/faucet' });
  }
  async openBitFun() {
    await Browser.open({ toolbarColor: '#f4dc41', url: 'http://bitfun.co/games' });
  }

}
